/* --------------------- Base Styles -------------------- */
@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Quicksand", arial, sans-serif;
}

body {
  height: 90vh;
  background: #e2e2e2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a,
li {
  text-decoration: none;
  list-style: none;
}

h3 {
  font-size: 24px;
}

.btn {
  background: #0A285F;
  border: 3px solid #e2e2e2;
  border-radius: 5px;
  width: 100px;
  height: 50px;
  color: #e2e2e2;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}
.btn:hover {
  border: 3px solid #FFCC00;
  transform: scale(1.02);
}

/* -------------------- Discover Page ------------------- */
.input-container {
  margin-bottom: 1rem;
}
.input-container button {
  width: 100%;
  border: 3px solid transparent;
  border-radius: 5px;
}
.input-container h4 {
  margin: 1.5rem 0;
  text-align: center;
  border-bottom: 2px solid lightgrey;
  height: 0.7rem;
}
.input-container h4 span {
  display: inline-block;
  background: #e2e2e2;
  color: grey;
  padding: 0 1rem;
}
.input-container form {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.input-container form input {
  width: 80%;
  outline: none;
  border: none;
  padding: 0.8rem 0.8rem;
  font-size: 14px;
  font-weight: 500;
  border-radius: 5px 0 0 5px;
}
.input-container form button {
  background: #0A285F;
  color: #e2e2e2;
  border: none;
  border-radius: 0 5px 5px 0;
  width: 20%;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}
.input-container form button:active {
  color: #FFCC00;
}

/* ----------------------- Loader ----------------------- */
@-webkit-keyframes pulse {
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1.5);
  }
}
@keyframes pulse {
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1.5);
  }
}
.loader {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1vh;
  top: 10vh;
  background-color: #0A285F;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  z-index: 1000;
}

.loader-svg {
  font-size: 12rem;
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

.loader-small {
  position: relative;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

/* ----------------------- Header ----------------------- */
.header {
  height: 10vh;
  background: #0A285F;
}

.header-container {
  height: 10vh;
  padding: 1rem;
  align-items: center;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.header-container a {
  width: 25%;
  color: #e2e2e2;
  font-size: 1.5rem;
  transition: all 0.2s ease;
}
.header-container a:hover {
  transform: scale(1.05);
  color: #FFCC00;
}
.header-container div {
  width: 50%;
  display: flex;
  justify-content: center;
}
.header-container div a {
  width: 100%;
  display: flex;
  justify-content: center;
}
.header-container div a img {
  width: 100px;
}
.header-container img {
  width: 20%;
}
.header-container .link-discover {
  display: flex;
  justify-content: flex-end;
}

@media screen and (max-width: 350px) {
  .header {
    height: 20vh;
  }

  .header-container {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .header-container div {
    width: 100%;
  }
  .header-container div a {
    justify-content: center;
  }
  .header-container .link-discover {
    justify-content: center;
  }
}
/* --------------------- Body Layout -------------------- */
.body-container {
  padding: 2rem;
  margin: auto;
}

/* ------------------------ Home ------------------------ */
.pokedex-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: auto;
}

.background-image {
  background: url("../assets/images/all-pokemons.png");
  position: absolute;
  top: 0;
  bottom: -10vh;
  left: 0;
  right: 0;
  z-index: -1;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  opacity: 0.3;
  filter: blur(1px);
}

.home-content {
  height: 70vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home-content h1 {
  font-size: 64px;
}

/* -------------------- Pokedex Page -------------------- */
.filter-cards h2 {
  font-size: 0.8rem;
}
.filter-cards img {
  width: 100%;
  height: 30vh;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 1rem;
  margin-bottom: 1rem;
}

.filter-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.filter-container button {
  margin: 0.5rem;
  padding: 0.5rem 1rem;
  border: 1px solid #0A285F;
  border-radius: 1rem;
  background: #e2e2e2;
  color: #0A285F;
  font-weight: bold;
  cursor: pointer;
}
.filter-container button:active {
  background: #0A285F;
  color: white;
}

