  border-radius: 0.5rem;
  box-shadow: 3px 3px 10px 6px rgba(0, 0, 0, 0.06);
  font-weight: 500;
  color: #0A285F;
  width: 100%;
  max-height: 50vh;
  overflow-y: scroll;
  z-index: 200;
  /* Track */
  /* Handle */
  /* Handle on hover */
}
.dropdown .dropdown-content::-webkit-scrollbar {
  width: 10px;
  position: absolute;
  transform: translateX(-50px);
}
.dropdown .dropdown-content::-webkit-scrollbar-track {
  background: #c2c2c2;
  border-radius: 0.5rem;
}
.dropdown .dropdown-content::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 0.5rem;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.dropdown .dropdown-content::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.dropdown .dropdown-content .dropdown-item {
  padding: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: #0A285F;
  border: 1px solid transparent;
  border-radius: 2rem;
  margin: 0.2rem 0;
}
.dropdown .dropdown-content .dropdown-item:hover {
  border: 1px solid #0A285F;
}

.box {
  border: 2px solid transparent;
  border-radius: 1rem;
  box-shadow: 2px 2px 20px 2px rgba(0, 0, 0, 0.3);
  color: #e2e2e2;
  width: 200px;
  height: 300px;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #0A285F;
  transition: all 0.2s ease;
}
.box:hover {
  border: 2px solid #0A285F;
  background: #FFCC00;
  color: black;
  transform: scale(1.1);
}
.box .number small {
  font-weight: bold;
  font-size: 2rem;
}
.box img {
  width: 100%;
  height: 70%;
  padding: 0.5rem;
}

.load-more-message {
  margin: 2rem 0;
  display: flex;
  justify-content: center;
}

.load-more {
  width: 100%;
  align-self: center;
  height: 4rem;
  font-size: 1.5rem;
}

/* -------------------- Pokemon Page -------------------- */
.card {
  width: 100%;
  background: lightgrey;
  border: 1px solid lightgrey;
  border-radius: 1rem;
  box-shadow: 1px 1px 10px 2px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1.5rem;
}

.img-container {
  width: 100%;
  margin: auto;
  background: linear-gradient(45deg, #e2e2e2, #FFCC00);
  border-radius: 1rem;
}
.img-container img {
  width: 100%;
}

.detail-bio {
  width: 100%;
}
.detail-bio .bio h1 small {
  color: grey;
}
.detail-bio .bio div {
  display: flex;
  justify-content: space-between;
  padding: 0.25rem 1rem;
}

.detail {
  padding: 0.25rem 1rem;
  margin: 2rem 0;
}
.detail h3 {
  margin-bottom: 0.5rem;
}

.detail-stats {
  margin: 2rem 0 0 0;
}
.detail-stats div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.2rem 0.5rem;
}

.detail-info {
  width: 100%;
}
.detail-info .info-paragraph {
  background-color: #c2c2c2;
  margin: 0.5rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
}
.detail-info .info h1 {
  margin: 1rem 0.5rem;
}
.detail-info .info h1 small {
  color: grey;
}
.detail-info .info div {
  display: flex;
  justify-content: space-between;
  padding: 0.25rem 1rem;
}

.detail-ability div {
  margin-bottom: 2rem;
}

.detail-sprites div img {
  width: 150px;
}
.detail-sprites .sprites {
  display: flex;
  justify-content: center;
  border: 1px solid #c2c2c2;
  border-radius: 0.5rem;
  flex-wrap: wrap;
}

@media screen and (min-width: 430px) {
  .img-container {
    max-width: 400px;
  }

  .pokedex-container {
    max-width: 100%;
  }
}
@media screen and (min-width: 800px) {
  .header-container {
    max-width: 1200px;
  }
  .header-container div a img {
    width: 200px;
  }

  .background-image {
    bottom: 0;
  }

  .body-container {
    max-width: 1200px;
  }

  .pokedex-container {
    width: 1200px;
  }

  .pokemon-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .discover-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
    gap: 0.5rem;
  }

  .input-container {
    width: 25%;
    margin-top: 4.25rem;
  }

  .card {
    max-width: 800px;
    padding-bottom: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
  .card .detail {
    margin: 0.5rem 0;
  }
  .card .detail-bio {
    grid-column: 1/1;
    grid-row: 1/span 2;
  }
  .card .detail-info {
    grid-column: 2/2;
    grid-row: 1/span 2;
  }
  .card .detail-sprites {
    grid-column: 2/2;
    grid-row: 3/3;
  }
  .card .detail-ability {
    grid-column: 1/1;
    grid-row: 3/3;
  }
